import "./sidebar.css";

import React from 'react'
import { NavLink } from 'react-router-dom'

const Sidebar = ({sidebar}) => {
    

    

    return (
    <>
            <div className={`sidebar m-0 p-2 pt-0 d-flex ${sidebar}`}>
                <div className="container">
                    <div className="navbar">
                        <img className="awarno-logo m-3 mt-4 mb-5" src="/assets/sidebar/Crushin.png" alt="error" height="30px" />
                            <img className="A-logo mt-0 mb-3" src="/assets/sidebar/crushin_alt.png" alt="error" height="30px"  />
                                <ul className="nav nav-pills">
                                    <li className="nav-item">
                                        <NavLink className="nav-link navhover text-white d-flex" aria-current="page" to="/">
                                        <div className="d-flex align-items-center justify-content-center">
                                                <img src="/assets/sidebar/dashboard.png" alt="" className="img" />
                                                <img src="/assets/sidebar/darhboard_alt.png" alt="" className="active_img" />
                                            </div>
                                            
                                            <span className='fw-bold ms-1'> Dashboard</span></NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link navhover text-white d-flex" to="/user/list">
                                            <div className="d-flex align-items-center justify-content-center">
                                                <img src="/assets/sidebar/User.png" alt="" className="img" />
                                                <img src="/assets/sidebar/User-alt.png" alt="" className="active_img" />
                                            </div>
                                            
                                            <span className='fw-bold ms-1'> User List</span></NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link navhover text-white d-flex" to="/transaction">
                                        <div className="d-flex align-items-center justify-content-center">
                                                <img src="/assets/sidebar/Money.png" alt="" className="img" />
                                                <img src="/assets/sidebar/Money-alt.png" alt="" className="active_img" />
                                            </div>
                                            
                                            <span className='fw-bold ms-1'> Transactions</span></NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link navhover text-white d-flex" to="/match/details">
                                        <div className="d-flex align-items-center justify-content-center">
                                                <img src="/assets/sidebar/Favorite.png" alt="" className="img" />
                                                <img src="/assets/sidebar/Favorite-alt.png" alt="" className="active_img" />
                                            </div>
                                            
                                            <span className='fw-bold ms-1'> Match Details</span></NavLink>
                                    </li>

                                    {/* <li className="nav-item">
                                        <NavLink className="nav-link navhover text-white" to="/wallet">
                                            <img src="/assets/sidebar/dashboard.png" alt="" className="img" />
                                            <img src="/assets/sidebar/Dimond-alt.png" alt="" className="active_img" />
                                            <span className='fw-bold'> Wallet</span></NavLink>
                                    </li> */}
                                    <li className="nav-item">
                                        <NavLink className="nav-link navhover text-white d-flex" to="/refferal">
                                        <div className="d-flex align-items-center justify-content-center">
                                                <img src="/assets/sidebar/Gift.png" alt="" className="img" />
                                                <img src="/assets/sidebar/Gift-alt.png" alt="" className="active_img" />
                                            </div>
                                            
                                            <span className='fw-bold ms-1'> Refferals</span></NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className="nav-link navhover text-white d-flex" to="/purchase">
                                            <div className="d-flex align-items-center justify-content-center">
                                                <img src="/assets/sidebar/Dimond.png" alt="" className="img" />
                                                <img src="/assets/sidebar/Dimond-alt.png" alt="" className="active_img" />
                                            </div>
                                            <span className='fw-bold ms-1'> Superlike Purchase </span></NavLink>
                                    </li>
                                </ul>
                                
                            </div>
                    </div>
                </div>
                
    </>
            )
}

export default Sidebar