import React, { useState } from 'react'
import "./add-user.css"
import Sidebar from "../../components/sidebar/sidebar";
import { NavLink } from 'react-bootstrap';
import Dropzone from "../../components/dropzone/dropzone";
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import { MultiSelect } from "react-multi-select-component";
import Row from 'react-bootstrap/Row';


const options = [
  { label: "Design", value: "Design" },
  { label: "Photograhy", value: "Photograhy" },
];

const Adduser = () => {

  const [selected, setSelected] = useState([]);
  const [sidebar, setSidebar] = useState(false);

  function sidebarClick() {
    setSidebar(!sidebar);
  }
  let sidebarCheck = sidebar ? "active" : "";
  

  return (
    <>
      <div className="body_wraper">
            <div className="body_header">
              <i className={`fa-solid fa-bars show_sidebar ${sidebar}`} onClick={sidebarClick}></i>
              <i className={`fa-solid fa-xmark ms-auto me-4 hide_sidebar ${sidebar}`} onClick={sidebarClick}></i>
            </div>
        <div className="container-fluied projects d-flex">
          <Sidebar  sidebar={sidebarCheck} />
          <div className="dashboard_main_container ms-sm-auto">
            <div className="container-fluid dashboard_main_wraper px-0">
              <div className="header">
                <div className="d-flex align-items-center mb-4">
                  <img src="/assets/Menu.png" alt="" className="me-3 mb-2" />
                  <h2 className="fw-bold">Add User</h2>
                </div>
              </div>
              <div className="">
                  <div className="head">Personal Info</div>
                  <Form>
                    <Row>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridEmail">
                        <Form.Label>Name</Form.Label>
                        <Form.Control type="text" placeholder="Ex Nishant" />
                      </Form.Group>

                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>Phone No</Form.Label>
                        <Form.Control type="number" placeholder="Ex 7503063585" />
                      </Form.Group>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>Email ID</Form.Label>
                        <Form.Control type="email" placeholder="Ex Nishant@gmail.com" />
                      </Form.Group>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>Gender</Form.Label>
                        <Form.Select  aria-label="Default select example">
                          <option>Gender</option>
                          <option value="1">Male</option>
                          <option value="2">Female</option>
                          <option value="3">Others</option>
                        </Form.Select>
                      </Form.Group>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>Why are you here ?</Form.Label>
                        <Form.Select  aria-label="Default select example">
                          <option>Select</option>
                          <option value="1">For Casual</option>
                          <option value="2">For Serious Relationship</option>
                        </Form.Select>
                      </Form.Group>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>Who would you like to date ?</Form.Label>
                        <Form.Select  aria-label="Default select example">
                          <option>Select</option>
                          <option value="1">Male</option>
                          <option value="2">Female</option>
                          <option value="3">Others</option>
                        </Form.Select>
                      </Form.Group>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>Do You Drink ?</Form.Label>
                        <Form.Select  aria-label="Default select example">
                          <option>Select</option>
                          <option value="1">Yes</option>
                          <option value="2">No</option>
                        </Form.Select>
                      </Form.Group>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>Do You Smoke ?</Form.Label>
                        <Form.Select  aria-label="Default select example">
                          <option>Select</option>
                          <option value="1">Yes</option>
                          <option value="2">No</option>
                        </Form.Select>
                      </Form.Group>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>Do you Follow a religion ?</Form.Label>
                        <Form.Select  aria-label="Default select example">
                          <option>Religion</option>
                          <option value="1">Yes</option>
                          <option value="2">No</option>
                        </Form.Select>
                      </Form.Group>
                      <Form.Group as={Col} sm={12} md={6} className="mb-md-4 mb-3" controlId="formGridPassword">
                        <Form.Label>What are your political view ?</Form.Label>
                        <Form.Control type="text" placeholder="Ex A Polictical " />
                      </Form.Group>
                    </Row>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                      <Form.Label>Add a profile bio</Form.Label>
                      <Form.Control as="textarea" rows={3} />
                    </Form.Group>

                    <MultiSelect options={options} value={selected} onChange={setSelected} labelledBy="Interests" />


                    <div className=" my-5 ">
                      <h3 className='heading'>Profile Photos</h3>
                      <div className="dropzone-container">
                      <Dropzone />
                      </div>
                    </div>
                    <div className="btn-container d-flex justify-content-end pb-4">
                      <NavLink className="btn cancel-btn mx-3 ">Cancel</NavLink>
                      <NavLink className="btn ">Create</NavLink>
                    </div>
                  </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Adduser;