import React, { useState } from 'react'
import { Line } from "react-chartjs-2"
import { Chart as ChartJS, Title, Tooltip, LineElement, Legend, LinearScale, CategoryScale, PointElement, Filler } from "chart.js"

ChartJS.register(
     Title, Tooltip, LineElement, Legend, LinearScale, PointElement, Filler, CategoryScale
)



const Graph = () => {

let gradient;
function getGradient(ctx, chartArea) {
 
  
  if (!gradient) {
    gradient = ctx.createLinearGradient(0, 0, 0, 400);
    gradient.addColorStop(1, 'rgba(255, 248, 250)');
    gradient.addColorStop(0.8, 'rgba(252, 204, 218)');
    gradient.addColorStop(0.6, 'rgba(252, 179, 200)');
    gradient.addColorStop(0.4, 'rgba(255, 156, 184)');
    gradient.addColorStop(0.2, 'rgba(252, 129, 164)');
    gradient.addColorStop(0, 'rgba(255, 109, 149)');
  }

  return gradient;
}



const [data, setData] = useState({
    labels: ["1pm-3pm", "1pm-3pm", "4pm-7pm", "7pm-10pm", "11pm-1am", "1am-3am", "4pm-7pm", "10am-1pm"],
    datasets:[{
        label:"User Activity",
        
        data:[10, 5, 22, 15, 20, 12, 16, 10],
        backgroundColor: function(context) {
            const chart = context.chart;
            const {ctx, chartArea} = chart;
    
            if (!chartArea) {
              // This case happens on initial chart load
              return;
            }
            return getGradient(ctx, chartArea);
          },
        boderColor:'#D9164B',
        tension:0.4,
        fill: true,
        pointStyle:'circle',
        pointBorderColor:'blue',
        
    }],
    options: {
      maintainAspectRatio: false,
    }


})

  return (
    <>
        <Line data={data}></Line>
    </>
  )
}

export default Graph