import React, { acceptedFiles } from "react";
import { NavLink } from "react-bootstrap";
import { useDropzone } from "react-dropzone";




function Dropzone(props) {
  const {acceptedFiles, getRootProps, getInputProps} = useDropzone();
  
  const files = acceptedFiles.map(file => (
    <NavLink key={file.path}>
      {file.path} - {file.size} bytes
    </NavLink>
  ));

  return (
    <>
      <div className="d-flex justify-content-center flex-column align-items-center">
        <h3 className='heading mb-4'>Upload Your Files</h3>
        <p className="mb-4">File should be in Jpeg,Png</p>
        <div {...getRootProps({ className: "dropzone" })}>
                              <input className="input-zone" {...getInputProps()} />
                              <div className="text-center">
                                <img src="../assets/upload.png" alt="" className="" />
                                <p className="dropzone-content">
                                  Dragndrop some files here, or click to select files
                                </p>
                                
                              </div>
          </div>
      </div>
      <div className="my-4">
                          <h3 className="drop-foot-heading fw-bold ">Uploaded Files</h3>
                          <p className="dropzone-uploaded-file"><img src="../assets/subtract.png" alt="" className="me-3" />{files}</p>
      </div>
      
    </>
  );
}
export default Dropzone;